import React from 'react';

export const useIsomorphicEffect = typeof window === 'undefined'
  ? React.useEffect
  : React.useLayoutEffect;

interface Value<T> {
  value: T;
}

export function useConstant<T>(supplier: () => T): T {
  const ref = React.useRef<Value<T>>();

  if (!ref.current) {
    ref.current = {
      value: supplier(),
    };
  }

  return ref.current.value;
}

export function useConstantCallback<T extends((...args: any) => any)>(callback: T): T {
  return useConstant(() => callback);
}

export function useIntersectionObserver(
  reference: React.MutableRefObject<HTMLElement | null | undefined | {}>,
  callback: (entry: IntersectionObserverEntry) => boolean,
): void {
  React.useEffect(() => {
    const el = reference.current;

    if (el && el instanceof HTMLElement) {
      const observer = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting && callback(entry)) {
            observer.unobserve(el);
          }
        });
      });

      observer.observe(el);

      return (): void => {
        observer.unobserve(el);
      };
    }
    return undefined;
  }, [reference, callback]);
}
